<template>
<div class="flex flex-wrap gap-4">
    <button :class="button.styleClass" v-for="(button, index) in buttons" :key="index" @click="clickButton(button)">{{ button.text }}</button>
</div>
</template>

<script>
import {
    ref
} from "vue";

export default {
    props: {
        content: Array
    },
    setup(props, {
        emit
    }) {
        const buttons = ref(props.content);
        buttons.value.forEach(function (button) {
            button.styleClass = ["button-37"];
            if (button.other) {
                button.styleClass.push("button-color-2");
            } else {
                button.styleClass.push("button-color-1");
            }
        });

        const clickButton = function (button) {
            emit("buttonClicked", button)
        };

        return {
            buttons,
            clickButton
        };
    }
}
</script>

<style scoped>
.button-37 {
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, .1) 0 2px 4px 0;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    outline: none;
    outline: 0;
    padding: 8px 20px;
    text-align: center;
    transform: translateY(0);
    transition: transform 150ms, box-shadow 150ms;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.button-37-on {
    background-color: #fff;
    border: 1px solid #13aa52;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, .1) 0 2px 4px 0;
    box-sizing: border-box;
    color: #000;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    outline: none;
    outline: 0;
    padding: 8px 20px;
    text-align: center;
}

.button-37:hover {
    box-shadow: rgba(0, 0, 0, .15) 0 3px 9px 0;
    transform: translateY(-3px);
}

.button-color-1 {
    background-color: #13aa52;
    border: 1px solid #13aa52;
}

.button-color-2 {
    background-color: #3B4FBF;
    border: 1px solid #3B4FBF;
}
</style>
