import MyFunctions from "@/logics/functions.js";
import Services from "@/logics/services.js";
import NodeTypes from "@/values/NodeTypes.js";
import {
    v4 as uuidv4
} from "uuid";

export default {
    store: new Map(),
    data: new Map(),
    canvasNodes: null,
    canvasNodesMap: null,
    conversation: null,
    actions: [],
    reset(sections, canvasNodes, canvasNodesMap, conversation) {
        const self = this;
        this.store.clear();
        this.data.clear();
        this.actions = [];
        sections.forEach(function (section) {
            if (section.id) {
                self.data.set(section.id, section);
            }
        });
        self.canvasNodes = canvasNodes;
        self.canvasNodesMap = canvasNodesMap;
        self.conversation = conversation;
    },
    proceed(id, callback) {
        const self = this;
        if (!self.data.has(id)) {
            alert("Course data is not complete!");
            return;
        }
        callback.removeNode();

        setTimeout(async function () {
            const section = self.data.get(id);

            if (section.info) {
                let info = section.info;
                if (section.varNames) {
                    info = MyFunctions.assignVar(info, section.varNames, self.store);
                }
                self.addTextNode(info);
            }
            console.log("section: " + section.id);
            self.actions = JSON.parse(JSON.stringify(section.actions))
            await self.performActions(callback);
        }, 1000);
    },
    async performActions(callback) {
        const self = this;
        while (self.actions.length) {
            const act = self.actions.shift();
            console.log(act.type);

            if (act.type === "next") {
                self.proceed(act.next, callback);
                break;

            } else if (act.type === "saveInput") {
                if (!self.store.has("input")) {
                    callback.unlockUserInput();
                    self.actions.unshift(act); //Put the action back for processing user input
                    break; //breakable action
                }
                self.store.set(act.varName, self.store.get("input"));
                self.store.delete("input");

            } else if (act.type === "rephrase") {
                const inputText = MyFunctions.assignVar(act.input, act.varNames, self.store);
                console.log("rephrase:" + inputText);
                const reply = await Services.serviceRephrase(inputText, 3, 10);
                self.store.set(act.save, reply);
                const confirm = MyFunctions.assignVar(act.text, act.varNames, self.store);
                self.addTextNode(confirm);
                const buttonContent = [];
                act.options.forEach(function (option) {
                    buttonContent.push({
                        text: option.text,
                        on: false,
                        next: option.next,
                        reply: option.reply,
                        other: option.other
                    });
                });
                self.addButtonNode(buttonContent);

            } else if (act.type === "options") {
                const buttonContent = [];
                act.options.forEach(function (option) {
                    buttonContent.push({
                        text: option.text,
                        on: false,
                        next: option.next,
                        reply: option.reply,
                        save: option.save,
                        other: option.other
                    });
                });
                self.addButtonNode(buttonContent);

            } else if (act.type === "publish") {
                let publishText = MyFunctions.assignVar(act.text, act.varNames, self.store);
                callback.addCanvasNode(publishText, act.varNames[0]);

            } else if (act.type === "writeFromSeed") {
                const seed = MyFunctions.assignVar(act.seed, act.varNames, self.store);
                const reply = await Services.serviceWriteFromSeed(seed, act.comp, act.concept, act.grade, act.maxWords);
                self.store.set(act.save, reply);
                self.addTextNode(reply);

            } else if (act.type === "listIdeasFromIdea") {
                const idea = MyFunctions.assignVar(act.idea, act.varNames, self.store);
                const reply = await Services.serviceListIdeasFromIdea(idea, act.term, act.desc, act.number);
                const items = Object.values(JSON.parse(reply))[0];
                const buttonContent = [];
                items.forEach(function (item) {
                    buttonContent.push({
                        text: item,
                        on: false,
                        next: "",
                        reply: "",
                        save: act.save,
                        append: act.append
                    });
                });
                self.addButtonNode(buttonContent);
                break; //breakable action

            } else if (act.type === "listStoryIdeas") {
                const seed = MyFunctions.assignVar(act.seed, act.varNames, self.store);
                const reply = await Services.serviceListStoryIdeas(seed, act.number, act.grade, act.maxWords);
                const ideas = Object.values(JSON.parse(reply))[0];
                const buttonContent = [];
                ideas.forEach(function (idea) {
                    buttonContent.push({
                        text: idea,
                        on: false,
                        next: "",
                        reply: "",
                        save: act.save
                    });
                });
                if (act.options) {
                    act.options.forEach(function (option) {
                        buttonContent.push({
                            text: option.text,
                            on: false,
                            next: option.next,
                            reply: option.reply,
                            save: option.save,
                            other: option.other
                        });
                    });
                }
                self.addButtonNode(buttonContent);
                break; //breakable action

            } else if (act.type === "makeImage") {
                const text = MyFunctions.assignVar(act.text, act.varNames, self.store);
                const images = await Services.serviceMakeImage(text);
                callback.addImageNode(images);

            } else if (act.type === "gradeWriting") {
                const text = MyFunctions.assignVar(act.text, act.varNames, self.store);
                const reply = await Services.serviceGradeWriting(text);
                self.addTextNode(reply);

            } else if (act.type === "removeVar") {
                const varSet = new Set();
                act.varNames.forEach(function (name) {
                    varSet.add(name);
                });
                callback.removeNodes(varSet);

            } else if (act.type === "drawInfer") {
                callback.addCanvasConnection(act.from, act.to);

            } else if (act.type === "exportConver") {
                let textOut = "";
                let index = self.conversation.length - 1;
                while (index > 0) {
                    const node = self.conversation[index];
                    if (NodeTypes.text === node.type) {
                        if (node.self) {
                            textOut += "<div>Me:<br/>{content}</div><br/>".replace("{content}", node.content[0]);
                        } else {
                            textOut += "<div>Bot:<br/>{content}</div><br/>".replace("{content}", node.content[0]);
                        }
                    }
                    index -= 1;
                }
                self.saveTextFile(textOut, "conversation.html");

            } else if (act.type === "save") {
                let textOut = "";
                self.canvasNodes.forEach(function (node) {
                    textOut += node.varName + "\n";
                    textOut += node.content + "\n\n";
                });
                self.saveTextFile(textOut, "my_story.txt");

            } else {
                alert("Unknown action: " + act.type);
            }
        }
        callback.actionDone();
    },
    saveTextFile(content, name) {
        const blob = new Blob([content], {
            type: "text/plain;charset=utf-8"
        });
        const a = document.createElement("a");
        document.body.appendChild(a);
        a.style = "display: none";
        const url = URL.createObjectURL(blob);
        a.href = url;
        a.download = name;
        a.click();
        URL.revokeObjectURL(url);
        a.remove();
    },
    addTextNode(text) {
        this.conversation.unshift({
            content: [text],
            self: false,
            type: NodeTypes.text,
            nodeId: uuidv4()
        });
    },
    addButtonNode(buttons) {
        this.conversation.unshift({
            content: buttons,
            self: true,
            type: NodeTypes.buttons,
            nodeId: uuidv4()
        });
    }
}