<template>
<van-dialog v-model:show="show" title="Editor Window" :showConfirmButton="true" :closeOnClickOverlay="true" width="640" :showCancelButton="true" @confirm="confirm()">
    <div class="p-2 h-96">
        <textarea type="textArea" class="border w-full h-48" v-model="userText"></textarea>
        <van-slider class="mt-4" v-model="height" :min="80" :max="500">
            <template #button>
                <div class="p-1 bg-blue-600 text-white rounded-lg select-none w-24">height: {{ height }}</div>
            </template>
        </van-slider>
    </div>
</van-dialog>
</template>

<script>
import Processor from "@/logics/lessonProcessor.js";
import {
    ref
} from "vue";
import {
    Dialog as VanDialog,
    Button,
    Slider
} from "vant";

export default {
    components: {
        [Button.name]: Button,
        [VanDialog.name]: VanDialog,
        [Slider.name]: Slider
    },
    setup(props, {
        emit
    }) {
        const show = ref(false);
        const userText = ref("");
        const height = ref(0);
        let theNode = null;

        const showDialog = function (node) {
            theNode = node;
            userText.value = node.content;
            height.value = node.h;
            show.value = true;
        };

        const confirm = function () {
            theNode.content = userText.value;
            if (theNode.varName) {
                Processor.store.set(theNode.varName, userText.value);
            }
            theNode.h = height.value;
            show.value = false;
            if (!userText.value) {
                emit("removeNode");
            }
        };

        return {
            show,
            userText,
            confirm,
            showDialog,
            height
        };
    }
}
</script>
