export default {
    api: null,

    async serviceRephrase(input, grade, maxWords) {
        const prompt = "rephrase this text '{input}' with correct grammar, that is understandable by {grade}-grade students, and do not use more than {maxWords} words.";

        const request = prompt.replace("{input}", input).replace("{grade}", grade).replace("{maxWords}", maxWords);

        const completion = await this.api.createChatCompletion({
            model: "gpt-3.5-turbo",
            messages: [
                { role: "user", content: request }],
        });
        console.log(completion);
        return completion.data.choices[0].message.content;
    },

    async serviceWriteFromSeed(seed, targetComponent, conceptName, grade, maxWords) {
        const prompt = "Write a '{targetComponent}' part for a {conceptName} based on this content '{seed}'. Do not exceed {maxWords} words.";
        const request = prompt.replace("{targetComponent}", targetComponent).replace("{conceptName}", conceptName).replace("{maxWords}", maxWords).replace("{seed}", seed);
        console.log(request);

        const system = "You are a teacher to teach students how to make creative writing, you will generate appropriate exemplary writing for {grade}-grade students to follow.";
        const systemRequest = system.replace("{grade}", grade);

        const completion = await this.api.createChatCompletion({
            model: "gpt-3.5-turbo",
            messages: [
                { role: "system", content: systemRequest },
                { role: "user", content: request }
            ]
        });
        console.log(completion);
        return completion.data.choices[0].message.content;
    },

    async serviceListIdeasFromIdea(idea, term, description, number) {
        const prompt = "Based on the idea of '{idea}', list {number} {term} which could be '{description}' in this idea.";

        const request = prompt.replace("{idea}", idea).replace("{term}", term).replace("{number}", number).replace("{description}", description);
        console.log(request);

        const completion = await this.api.createChatCompletion({
            model: "gpt-3.5-turbo",
            messages: [
                { role: "system", content: "You are an idea generator, and you only reply with JSON format. Only write out the result as a single array of Strings within a JSON string array under the attribute named 'items'." },
                { role: "user", content: request }
            ]
        });
        console.log(completion);
        return completion.data.choices[0].message.content;
    },

    async serviceListStoryIdeas(seed, number, grade, maxWords) {
        const prompt = "List {number} story ideas about '{seed}', which are appropriate for {grade}-grade school students. Describe each idea with only one short sentence, do not use more than {maxWords} words.";

        const request = prompt.replace("{seed}", seed).replace("{number}", number).replace("{grade}", grade).replace("{maxWords}", maxWords);
        console.log(request);

        const completion = await this.api.createChatCompletion({
            model: "gpt-3.5-turbo",
            messages: [
                { role: "system", content: "You are an idea generator, and you only reply with JSON format. Only write out the result as a single array of Strings within a JSON string array under the attribute named 'items'." },
                { role: "user", content: request }
            ]
        });
        console.log(completion);
        return completion.data.choices[0].message.content;
    },

    async serviceMakeImage(text) {
        const prompt = "image for kids, no words, no text, {text}".replace("{text}", text);
        console.log(prompt);
        const response = await this.api.createImage({
            prompt,
            n: 2,
            size: "512x512"
        });

        const images = [];
        response.data.data.forEach(function (image) {
            images.push(image.url);
        });
        return images;
    },

    async serviceGradeWriting(text) {
        const prompt = "Grade this writing '{text}', scale from 50 to 100 based on the quality of the writing. Provide a list of feedback on the clarity, quality, grammar, word choices, and sentence structure. Do not exceed 100 words.".replace("{text}", text);
        console.log(prompt);
        const system = "You are a teacher to teach students how to make creative writing, by providing appropriate feedback and encouraging grade. Your feedback must be easy to understand for 3-grade students.";

        const completion = await this.api.createChatCompletion({
            model: "gpt-3.5-turbo",
            messages: [
                { role: "system", content: system },
                { role: "user", content: prompt }
            ]
        });
        console.log(completion);
        return completion.data.choices[0].message.content;
    }
}