<template>
<svg :x="x" :y="y" :width="node.w" :height="node.h" @mousedown="mousedown" class="cursor-pointer" @mouseenter="mouseenter" @mouseleave="mouseleave" @mouseup="mouseup">
    <rect v-if="node.varName" :width="varTitleWidth" height="40" x="10" y="10" fill="#ffffff" fill-opacity="0.7" stroke-width="3" stroke="#00A3FE" rx="15">
    </rect>
    <text v-if="node.varName" x="20" y="35" ref="varTitle" class="var-title">{{ node.varName }}</text>
    <foreignObject :width="node.w" :height="node.h" x="15" y="50">
        <div v-if="node.type === NodeTypes.text" :class="node.class" class="px-3">{{ node.content }}</div>
        <div v-if="node.type === NodeTypes.image" :class="node.class">
            <img :width="node.w" :src="node.content" />
        </div>
    </foreignObject>
</svg>
</template>

<script>
import {
    ref,
    onMounted
} from "vue";
import NodeTypes from "@/values/NodeTypes.js";

export default {
    props: {
        node: Object
    },
    setup(props, {
        emit
    }) {
        const x = ref(props.node.x);
        const y = ref(props.node.y);
        let isMoving = false;
        const moveMe = function (mx, my) {
            isMoving = true;
            x.value += mx;
            y.value += my;
            emit("newPosition", {
                x: x.value,
                y: y.value,
                tw: varTitleWidth.value
            });
        };
        const mousedown = function (e) {
            e.stopPropagation();
            e.preventDefault();
            emit("onMouseDown", moveMe);
        };

        const mouseenter = function () {
            //console.log("enter");
        };
        const mouseleave = function () {
            //console.log("mouseleave");
        };
        const mouseup = function () {
            if (isMoving) {
                isMoving = false;
                return;
            }
            emit("onNodeClicked");
            isMoving = false;
        };

        const varTitle = ref(null);
        const varTitleWidth = ref(50);
        const updateTitleWidth = function () {
            if (props.node.varName && varTitle.value) {
                varTitleWidth.value = varTitle.value.getBBox().width + 20;
            }
        };
        onMounted(updateTitleWidth);

        return {
            NodeTypes,
            x,
            y,
            mousedown,
            mouseenter,
            mouseleave,
            mouseup,
            varTitle,
            varTitleWidth
        };
    }
}
</script>

<style scoped>
.var-title {
    fill: #00A3FE;
}
</style>
