<template>
<div class="flex flex-wrap">
    <div class="md:w-5/12 w-full mb-12">
        <div class="m-2 theme-color rounded-lg" @dragover="onDragover">
            <div class="flex flex-col items-center justify-end h-full p-4">
                <div class="w-full overflow-scroll flex flex-col-reverse">
                    <div ref="chatBottom"></div>
                    <div class="flex items-start py-2 mb-8" :class="{ 'flex-row-reverse': mes.self }" v-for="mes in history" :key="mes.nodeId">
                        <div>
                            <van-image v-if="mes.self" width="3rem" height="3rem" round fit="cover" src="images/test_me.png" />
                            <van-image v-else width="3rem" height="3rem" round fit="cover" src="images/test_ai.png" />
                        </div>
                        <div class="mx-4 p-4 bg-white rounded-lg">
                            <div class="cursor-move" :class="{ 'text-right': mes.self }">
                                <div v-if="mes.type === NodeTypes.text">
                                    <span v-html="mes.content[0]" draggable="true" @dragstart="startDrag(mes.content[0], mes.type)"></span>
                                </div>
                                <div v-if="mes.type === NodeTypes.image" class="flex gap-4">
                                    <img v-for="(url, index) in mes.content" :key="index" width="150" aspect-ratio="4/3" cover :src="url" draggable="true" @dragstart="startDrag(url, mes.type)" />
                                </div>
                                <div v-if="mes.type === NodeTypes.buttons">
                                    <my-buttons :content="mes.content" @button-clicked="pickOption" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="w-full">
                    <van-cell-group inset v-if="!sending">
                        <van-field v-model="chatToSend" label="" @keyup.enter="enterText()">
                            <template #button>
                                <van-button size="small" type="success" @click="enterText()">Send</van-button>
                            </template>
                        </van-field>
                    </van-cell-group>
                </div>
            </div>
        </div>
    </div>
    <div class="md:w-7/12 w-full">
        <div class="m-2 theme-color rounded-lg overflow-scroll" @dragover="onDragover" @drop="onDrop">
            <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="canvas" width="1200" height="1200" @mousemove="mosemove" @mouseup="mouseup">
                <defs>
                    <marker id="my_arrow" viewBox="0 0 10 10" refX="0" refY="5" markerWidth="3" markerHeight="3" orient="auto-start-reverse">
                        <path d="M 0 0 L 10 5 L 0 10 z" fill="#DC143C" fill-opacity="0.5" />
                    </marker>
                </defs>
                <my-node v-for="(node, index) in canvasNodes" :key="node.id" :node="node" @on-mouse-down="onNodeMouseDown" @on-node-clicked="clickCanvasNode(node, index)" @new-position="newPosition($event, node)" />
                <line v-for="(line, index) in connections" :key="index" :x1="line.sx" :y1="line.sy" :x2="line.ex" :y2="line.ey" stroke="#DC143C" stroke-width="6" stroke-opacity="0.5" marker-end="url(#my_arrow)" @click="removeLine(index)" />
            </svg>
        </div>
    </div>
</div>
<my-dialog ref="editor" @remove-node="removeCanvasNode()" />
</template>

<script>
import {
    v4 as uuidv4
} from "uuid";
import {
    ref,
    onMounted
} from "vue";
import {
    Button,
    Image as VanImage,
    Field,
    CellGroup,
    showToast
} from "vant";
import {
    Configuration,
    OpenAIApi
} from "openai";
import NodeTypes from "@/values/NodeTypes.js";
import MyNode from "@/components/MyNode.vue";
import MyButtons from "@/components/MyButtons.vue";
import Course from "@/values/lesson.json";
import Services from "@/logics/services.js";
import MyDialog from "@/components/MyDialog.vue";
import Processor from "@/logics/lessonProcessor.js";

export default {
    name: "HomeView",
    components: {
        MyNode,
        MyButtons,
        MyDialog,
        [Button.name]: Button,
        [VanImage.name]: VanImage,
        [Field.name]: Field,
        [CellGroup.name]: CellGroup
    },
    setup() {
        const userId = uuidv4();
        console.log(userId);
        const chatToSend = ref("");
        const history = ref([]);
        const sending = ref(true);
        const chatBottom = ref(null);
        const canvasNodes = ref([]);
        const canvasNodesMap = new Map();
        const editor = ref(null);
        const configuration = new Configuration({
            apiKey: NodeTypes.k2 + NodeTypes.k1 + NodeTypes.k3,
        });
        Services.api = new OpenAIApi(configuration);

        const connections = ref([]);
        const removeLine = function (index) {
            connections.value.splice(index, 1);
        };

        Processor.reset(Course.sections, canvasNodes.value, canvasNodesMap, history.value);
        const callback = {
            removeNode() {
                if (history.value[0]) {
                    if (history.value[0].type === NodeTypes.buttons) {
                        history.value.shift();
                    }
                }
                sending.value = true; //Always lock input textfield
            },
            unlockUserInput() {
                sending.value = false;
            },
            addImageNode(images) {
                history.value.unshift({
                    content: images,
                    self: false,
                    type: NodeTypes.image
                });
            },
            addCanvasNode(text, varName) {
                let x = 100;
                let y = 100;
                if (canvasNodes.value.length) {
                    x = canvasNodes.value[canvasNodes.value.length - 1].x;
                    y = canvasNodes.value[canvasNodes.value.length - 1].y;
                }
                const node = {
                    id: uuidv4(),
                    content: text,
                    x,
                    y: y + 100,
                    type: NodeTypes.text,
                    class: "text-blue-500 text-lg",
                    varName,
                    w: 400,
                    h: 80,
                    connStart: [],
                    connEnd: []
                };
                canvasNodes.value.push(node);
                canvasNodesMap.set(varName, node);
            },
            addCanvasConnection(from, to) {
                from.forEach(function (nameFrom) {
                    to.forEach(function (nameTo) {
                        const nodeStart = canvasNodesMap.get(nameFrom);
                        const nodeEnd = canvasNodesMap.get(nameTo);
                        if (nodeStart && nodeEnd) {
                            const endOffset = 20 * nodeEnd.connEnd.length;
                            const conn = {
                                sx: nodeStart.x + 50,
                                sy: nodeStart.y + 30,
                                ex: nodeEnd.x + endOffset + 10,
                                ey: nodeEnd.y,
                                endOffset
                            };
                            connections.value.push(conn);
                            nodeStart.connStart.push(conn);
                            nodeEnd.connEnd.push(conn);
                        }
                    });
                });
            },
            removeNodes(varSet) {
                let i = canvasNodes.value.length - 1;
                while (i >= 0) {
                    if (varSet.has(canvasNodes.value[i].varName)) {
                        canvasNodes.value.splice(i, 1);
                    }
                    i -= 1;
                }
            },
            actionDone() {
                setTimeout(function () {
                    chatBottom.value.scrollIntoView({
                        behavior: "smooth",
                        block: "end"
                    });
                }, 500);
            }
        };

        onMounted(function () {
            Processor.proceed("001", callback);
        });

        const pickOption = async function (button) {
            if (button.done) {
                return;
            }
            button.on = true;
            button.done = true;
            history.value.shift();
            history.value.unshift({
                content: [button.text],
                self: true,
                type: NodeTypes.text
            });

            if (button.reply) {
                history.value.unshift({
                    content: [button.reply],
                    self: false,
                    type: NodeTypes.text
                });
            }
            if (canvasNodes.value.length && button.cancel) {
                canvasNodes.value.pop(); //Remove last published canvas text
            }
            if (button.save) {
                Processor.store.set(button.save, button.text);
            }
            if (button.append) {
                const value = Processor.store.get(button.append);
                if (value) {
                    Processor.store.set(button.append, value + ", " + button.text);
                } else {
                    Processor.store.set(button.append, button.text);
                }
            }
            if (button.next) {
                Processor.proceed(button.next, callback);
            } else {
                await Processor.performActions(callback);
            }
        };

        const enterText = async function () {
            if (!chatToSend.value) {
                return;
            }
            const input = chatToSend.value;
            chatToSend.value = "";
            if (sending.value) {
                alert("We are not anticipating input text now...");
                return;
            }
            Processor.store.set("input", input);
            if (history.value[0]) {
                if (history.value[0].type === NodeTypes.buttons) {
                    history.value.shift();
                }
            }
            history.value.unshift({
                content: [input],
                self: true,
                type: NodeTypes.text
            });
            sending.value = true;
            await Processor.performActions(callback);
        };

        // const getChat = async function (input) {
        //     const response = await openai.createChatCompletion({
        //         model: "gpt-3.5-turbo",
        //         messages: [{
        //             role: "user",
        //             content: input
        //         }],
        //         user: userId
        //     });

        //     console.log(response.data);
        //     history.value.unshift({
        //         content: [response.data.choices[0].message.content],
        //         self: false,
        //         type: NodeTypes.text
        //     });
        // };

        // const getImages = async function (input) {
        //     const response = await openai.createImage({
        //         prompt: input,
        //         n: 2,
        //         size: "512x512",
        //         user: userId
        //     });

        //     const text = {
        //         content: [],
        //         self: false,
        //         type: NodeTypes.image
        //     };
        //     console.log(response.data);
        //     response.data.data.forEach(function (image) {
        //         text.content.push(image.url);
        //     });
        //     history.value.unshift(text);
        // };

        // const checkIntention = async function (input) {
        //     const prompt = Prompts.checkIntention.text.replace(Prompts.checkIntention.input, input);

        //     const response = await openai.createCompletion({
        //         model: "text-davinci-003",
        //         prompt,
        //         temperature: 0,
        //     });
        //     console.log(response);

        //     const check = response.data.choices[0].text.trim().toLowerCase();
        //     console.log(check);
        //     if (check.includes(Prompts.checkIntention.expectations[0])) {
        //         return 0;
        //     } else if (check.includes(Prompts.checkIntention.expectations[1])) {
        //         return 1;
        //     }
        //     alert("Unknown error");
        //     return -1;
        // };

        let draggingNode = null;

        const startDrag = function (content, type) {
            let w = 0;
            let h = 0;
            if (type === NodeTypes.text) {
                w = 400;
                h = 80;
            } else if (type === NodeTypes.image) {
                w = 150;
                h = 150;
            }

            draggingNode = {
                id: uuidv4(),
                content,
                class: "",
                type,
                w,
                h,
                connStart: [],
                connEnd: []
            }
        };

        const onDragover = function (e) {
            e.preventDefault();
        };

        const onDrop = function (e) {
            e.preventDefault();
            if (draggingNode) {
                const rect = e.target.getBoundingClientRect();
                draggingNode.x = e.clientX - rect.left;
                draggingNode.y = e.clientY - rect.top;
                canvasNodes.value.push(draggingNode);
                draggingNode = null;
            }
        };

        let nodeCallbackMove = null;
        const onNodeMouseDown = function (c) {
            nodeCallbackMove = c;
        };
        const newPosition = function ({
            x,
            y,
            tw
        }, node) {
            node.x = x;
            node.y = y;
            node.connStart.forEach(function (c) {
                c.sx = x + tw + 10;
                c.sy = y + 30;
            });
            node.connEnd.forEach(function (c) {
                c.ex = x + c.endOffset + 10;
                c.ey = y;
            });
        };
        let mx = 0;
        let my = 0;
        const mosemove = function (e) {
            if (nodeCallbackMove) {
                e.preventDefault();
                nodeCallbackMove(e.x - mx, e.y - my);
            }
            mx = e.x;
            my = e.y;
        };
        const mouseup = function () {
            nodeCallbackMove = null;
        };

        let indexClick = 0;
        const clickCanvasNode = function (node, index) {
            editor.value.showDialog(node);
            indexClick = index;
        };
        const removeCanvasNode = function () {
            canvasNodes.value.splice(indexClick, 1);
            showToast("Text removed");
        };

        return {
            NodeTypes,
            chatToSend,
            history,
            sending,
            startDrag,
            onDragover,
            onDrop,
            canvasNodes,
            onNodeMouseDown,
            mosemove,
            mouseup,
            pickOption,
            enterText,
            clickCanvasNode,
            chatBottom,
            newPosition,
            editor,
            removeCanvasNode,
            connections,
            removeLine
        };
    }
}
</script>

<style scoped>
.canvas {
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.theme-color {
    background-color: #e0e9f5;
    height: 85vh;
}
</style>
